.ImageLinkForm-wrap{
	padding: 60px 0;
	background: rgba(0, 0, 0, .3);
}

.ImageLinkForm-wrap p{
	margin-top: 0;
	color: #f0ead6;
}

.ImageLinkForm-wrap form input[type="text"]{
	padding: 10px;
	width: 30%;
	border-radius: 10px;
	background: transparent;
	border: 1px solid black;
	color: #f0ead6;
}

.ImageLinkForm-wrap form input[type="button"]{
    border: 0;
    line-height: normal;
    overflow: visible;
  
    -webkit-appearance: button; /* for input */
    -webkit-user-select: none; /* for button */
       -moz-user-select: none;
        -ms-user-select: none;
    padding: 10px;
	width: 100px;
	border-radius: 10px;
	cursor: pointer;
	margin-left: 5px;
	background: black;
	color: #f0ead6;
	transition: ease-in 200ms;
	text-transform: uppercase;
	letter-spacing: 2px;
	font-size: 12px;
	font-weight: 600;
}

.ImageLinkForm-wrap form input[type="button"]:hover{
   background: #f0ead6;
   -webkit-box-shadow: inset 0px 0px 3px 0px rgba(0,0,0,0.5);
-moz-box-shadow: inset 0px 0px 3px 0px rgba(0,0,0,0.5);
box-shadow: inset 0px 0px 3px 0px rgba(0,0,0,0.5);
color: #111;
}

input::-moz-focus-inner,
button::-moz-focus-inner {
    border: 0;
    padding: 0;
}


