body {
  margin: 0;
  padding: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;

  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
  background: #655091;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

textarea, select, input, button { outline: none; }
.ImageLinkForm-wrap{
	padding: 60px 0;
	background: rgba(0, 0, 0, .3);
}

.ImageLinkForm-wrap p{
	margin-top: 0;
	color: #f0ead6;
}

.ImageLinkForm-wrap form input[type="text"]{
	padding: 10px;
	width: 30%;
	border-radius: 10px;
	background: transparent;
	border: 1px solid black;
	color: #f0ead6;
}

.ImageLinkForm-wrap form input[type="button"]{
    border: 0;
    line-height: normal;
    overflow: visible;
  
    -webkit-appearance: button; /* for input */
    -webkit-user-select: none; /* for button */
       -moz-user-select: none;
        -ms-user-select: none;
    padding: 10px;
	width: 100px;
	border-radius: 10px;
	cursor: pointer;
	margin-left: 5px;
	background: black;
	color: #f0ead6;
	transition: ease-in 200ms;
	text-transform: uppercase;
	letter-spacing: 2px;
	font-size: 12px;
	font-weight: 600;
}

.ImageLinkForm-wrap form input[type="button"]:hover{
   background: #f0ead6;
box-shadow: inset 0px 0px 3px 0px rgba(0,0,0,0.5);
color: #111;
}

input::-moz-focus-inner,
button::-moz-focus-inner {
    border: 0;
    padding: 0;
}



.imageDisplay-wrap{
	display: flex; 
	justify-content: center;
	margin-top: 60px;
	width: 80%;
	margin: 60px auto;
}

.imageDisplay-wrap img{
	max-width: 100%;
	margin: 0 30px 30px 0;
	border-radius: 10px;
box-shadow: 2px 2px 3px 0px rgba(0,0,0,0.5);
}


.imageDisplay-wrap ul{
	list-style: none;
	color: #f0ead6;
	font-size: 18px;
	margin-top: 0;
	text-align: left;
}

.imageDisplay-wrap ul li{
	margin-bottom: 5px;
}

@media (max-width: 700px){
	.imageDisplay-wrap{
		flex-direction: column;
		width: 95%;
	}
	.imageDisplay-wrap ul{
		padding: 0;
		text-align: center;
		font-size: 16px;
	}
	.ImageLinkForm-wrap form{
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.ImageLinkForm-wrap form input{
		width: 80% !important;
	}
	.ImageLinkForm-wrap form input:nth-of-type(1){
		margin-bottom: 10px;
	}
}
.App {
  text-align: center;
}

.App-logo {
  -webkit-animation: App-logo-spin infinite 20s linear;
          animation: App-logo-spin infinite 20s linear;
  height: 40vmin;
}

.App-header {
  background-color: #282c34;
  min-height: 100vh;
  display: flex;
  flex-direction: column;
  align-items: center;
  justify-content: center;
  font-size: calc(10px + 2vmin);
  color: white;
}

.App-link {
  color: #61dafb;
}

@-webkit-keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

@keyframes App-logo-spin {
  from {
    -webkit-transform: rotate(0deg);
            transform: rotate(0deg);
  }
  to {
    -webkit-transform: rotate(360deg);
            transform: rotate(360deg);
  }
}

