.imageDisplay-wrap{
	display: flex; 
	justify-content: center;
	margin-top: 60px;
	width: 80%;
	margin: 60px auto;
}

.imageDisplay-wrap img{
	max-width: 100%;
	margin: 0 30px 30px 0;
	border-radius: 10px;
	-webkit-box-shadow: 2px 2px 3px 0px rgba(0,0,0,0.5);
-moz-box-shadow: 2px 2px 3px 0px rgba(0,0,0,0.5);
box-shadow: 2px 2px 3px 0px rgba(0,0,0,0.5);
}


.imageDisplay-wrap ul{
	list-style: none;
	color: #f0ead6;
	font-size: 18px;
	margin-top: 0;
	text-align: left;
}

.imageDisplay-wrap ul li{
	margin-bottom: 5px;
}

@media (max-width: 700px){
	.imageDisplay-wrap{
		flex-direction: column;
		width: 95%;
	}
	.imageDisplay-wrap ul{
		padding: 0;
		text-align: center;
		font-size: 16px;
	}
	.ImageLinkForm-wrap form{
		display: flex;
		flex-direction: column;
		align-items: center;
	}
	.ImageLinkForm-wrap form input{
		width: 80% !important;
	}
	.ImageLinkForm-wrap form input:nth-of-type(1){
		margin-bottom: 10px;
	}
}